import * as React from 'react';
import './App.css';
import { createTheme, responsiveFontSizes, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import TwitterIcon from '@mui/icons-material/Twitter';
import Typography from '@mui/material/Typography';
import logo from "./assets/images/godroll200.png";
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Stack from '@mui/material/Stack';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Link from '@mui/material/Link';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import ReactPlayer from 'react-player'
import Chip from '@mui/material/Chip';
import Divider from '@mui/material/Divider';





let theme = createTheme({
  palette: {
    typography: {
      h1: {
        fontWeight:'900'
      },
    },
    primary: {
      // light: will be calculated from palette.primary.main,
      main: '#000000',
      // dark: will be calculated from palette.primary.main,
      // contrastText: will be calculated to contrast with palette.primary.main
    },
    secondary: {
      light: '#0066ff',
      main: '#0044ff',
      // dark: will be calculated from palette.secondary.main,
      contrastText: '#ffcc00',
    },
    // Used by `getContrastText()` to maximize the contrast between
    // the background and the text.
    contrastThreshold: 3,
    // Used by the functions below to shift a color's luminance by approximately
    // two indexes within its tonal palette.
    // E.g., shift from Red 500 to Red 300 or Red 700.
    tonalOffset: 0.2,
  },
});

theme = responsiveFontSizes(theme);

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <div className="App">

        <AppBar elevation={0} variant="outlined" position="sticky" sx={{py:1,backgroundColor:"#ffffff", opacity: 0.97, borderTop: 0, borderLeft: 0, borderRight:0,}}>
          <Container maxWidth="lg">
          <Toolbar>
            <img src={logo} alt="Godroll Logo" sx={{ flexGrow: 1,  }} />
            <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>

             </Typography>

             <IconButton
                  size="large"
                  aria-label="show more"
                  color="primary"
                >
                  <TwitterIcon />
                </IconButton>
          </Toolbar>
          </Container>
        </AppBar>

        <main>
          {/* Hero unit */}
          <Box

          >
            <Container align="center" maxWidth="md"
            sx={{
              pt: 4,
              pb: 6,
            }}>
              <Typography

                variant="h1"
                align="center"
                color="text.primary"
                gutterBottom
                sx={{
                  fontWeight: 900,
                  backgroundClip: 'text',
                  textFillColor: "transparent",
                  backgroundImage: 'linear-gradient(90deg, #007CF0 20%, #00DFD8 80%)',
                }}
              >
                QUALITY. CREATIVE. COMMUNITY.
              </Typography>
              <Typography variant="h5" align="center" color="text.secondary" maxWidth="sm">
                <p>GodRoll builds Solana based projects, that unite, excite, and build the community.</p>
                <p>We believe every piece you recieve should be of quailty and depth (aka: a God Roll).</p>
              </Typography>
            </Container>
            <Container maxWidth="max" sx={{borderTop:1, borderTop: '1px solid rgba(0, 0, 0, 0.12)',bgcolor: '#fafafa'}}>
            <Container maxWidth="lg" sx={{py:8}}>
                <Grid container spacing={4} direction="row" alignItems="center">
                  <Grid item xs={12} sm={12} md={7}>
                    <Container sx={{alignItems: "center" }}>
                        <Typography gutterBottom variant="h3"
                        align="center"
                        color="text.primary"
                        gutterBottom
                        sx={{
                          fontWeight: 900,
                        }}>
                          JustShapes
                        </ Typography>

                        <Box sx={{ my: 3, mx: 2 }}>
                          <Chip label="In Development" sx={{ m: 1 }} Filled />
                          <Chip label="Generative Art" sx={{ m: 1 }}  Filled />
                        </Box>
                        <Grid container spacing={2}>
                         <Grid item xs={12} sm={12} md={12}>
                           <Divider>Summary</Divider>
                           <Typography>
                             <p>Generative art NFT collection for Solana. Designed around the human brain enjoying to watch smooth fluid rotations that loop perfectly.</p>
                             <p>There will be a range of different 3D shapes, colors, backgrounds, patterns, and movements. With different sets to collect and enjoy.</p>
                           </Typography>
                           <Box sx={{ mt: 6 }}>
                           <Divider>Features</Divider>
                           </Box>
                         </Grid>




                          <Grid item xs={12} sm={12} md={6}>
                            <Card variant="outlined" sx={{bgcolor:"#FAFAFA"}}>
                              <Box sx={{ color: 'text.primary', fontSize:'2.2rem', fontWeight: 900 }}>
                                60 FPS
                              </Box>
                              <Box sx={{ color: 'text.primary' }}>Silky Smooth</Box>
                            </Card>
                          </Grid>
                          <Grid item xs={12} sm={12} md={6}>
                            <Card variant="outlined" sx={{bgcolor:"#FAFAFA"}}>
                            <Box sx={{ color: 'text.primary', fontSize:'2.2rem', fontWeight: 900 }}>
                              10 SEC
                            </Box>
                            <Box sx={{ color: 'text.primary' }}>Seemless Looping</Box>
                            </Card>
                          </Grid>

                          <Grid item xs={12} sm={12} md={6}>
                            <Card variant="outlined" sx={{bgcolor:"#FAFAFA"}}>
                              <Box sx={{ color: 'text.primary', fontSize:'2.2rem', fontWeight: 900 }}>
                                MATH
                              </Box>
                              <Box sx={{ color: 'text.primary' }}>Based Movements</Box>
                            </Card>
                          </Grid>
                          <Grid item xs={12} sm={12} md={6}>
                            <Card variant="outlined" sx={{bgcolor:"#FAFAFA"}}>
                            <Box sx={{ color: 'text.primary', fontSize:'2.2rem', fontWeight: 900 }}>
                              MULTI-SETS
                            </Box>
                            <Box sx={{ color: 'text.primary' }}>For Collectors</Box>
                            </Card>
                          </Grid>

                        </Grid>

                        <Button variant="contained" disabled sx={{m:3}}>
                        View Project
                      </Button>
                      </ Container>
                  </ Grid>
                  <Grid item xs={12} sm={12} md={5} >
                    <Paper sx={{boxShadow:'0 8px 30px rgba(0,0,0,0.32)', borderRadius:'5px', p:3, lineHeight:0}} >
                      <ReactPlayer width='100%' height='100%' playing loop muted={true} controls={false} playsinline={true} url={'https://ss.simplestuff.co.nz/wp-content/uploads/2021/09/red.mp4'} type="video/mp4" />
                    </Paper>
                  </ Grid>
                </ Grid>
            </ Container>


            <Container maxWidth="lg" sx={{py:8}}>
                <Grid container spacing={4} direction="row" alignItems="center">
                  <Grid item xs={12} sm={12} md={7}>
                    <Container sx={{alignItems: "center" }}>
                        <Typography gutterBottom variant="h3"
                        align="center"
                        color="text.primary"
                        gutterBottom
                        sx={{
                          fontWeight: 900,
                        }}>
                          SolSilver
                        </ Typography>

                        <Box sx={{ my: 3, mx: 2 }}>
                          <Chip label="In Development" sx={{ m: 1 }} Filled />
                          <Chip label="Redemable NFT" sx={{ m: 1 }}  Filled />
                        </Box>
                        <Grid container spacing={2}>
                         <Grid item xs={12} sm={12} md={12}>
                           <Divider>Summary</Divider>
                           <Typography>
                             <p>Generative art NFT collection for Solana. Designed around the human brain enjoying to watch smooth fluid rotations that loop perfectly.</p>
                             <p>There will be a range of different 3D shapes, colors, backgrounds, patterns, and movements. With different sets to collect and enjoy.</p>
                           </Typography>
                           <Box sx={{ mt: 6 }}>
                           <Divider>Features</Divider>
                           </Box>
                         </Grid>


                          <Grid item xs={12} sm={12} md={6}>
                            <Card variant="outlined" sx={{bgcolor:"#FAFAFA"}}>
                              <Box sx={{ color: 'text.primary', fontSize:'2.2rem', fontWeight: 900 }}>
                                0.99999
                              </Box>
                              <Box sx={{ color: 'text.primary' }}>Pure Silver</Box>
                            </Card>
                          </Grid>
                          <Grid item xs={12} sm={12} md={6}>
                            <Card variant="outlined" sx={{bgcolor:"#FAFAFA"}}>
                            <Box sx={{ color: 'text.primary', fontSize:'2.2rem', fontWeight: 900 }}>
                              RARE
                            </Box>
                            <Box sx={{ color: 'text.primary' }}>Anodized Versions</Box>
                            </Card>
                          </Grid>

                          <Grid item xs={12} sm={12} md={6}>
                            <Card variant="outlined" sx={{bgcolor:"#FAFAFA"}}>
                              <Box sx={{ color: 'text.primary', fontSize:'2.2rem', fontWeight: 900 }}>
                                REDEAMABLE
                              </Box>
                              <Box sx={{ color: 'text.primary' }}>Solana Anodized</Box>
                            </Card>
                          </Grid>
                          <Grid item xs={12} sm={12} md={6}>
                            <Card variant="outlined" sx={{bgcolor:"#FAFAFA"}}>
                            <Box sx={{ color: 'text.primary', fontSize:'2.2rem', fontWeight: 900 }}>
                              LIMITED
                            </Box>
                            <Box sx={{ color: 'text.primary' }}>Yearly Sets</Box>
                            </Card>
                          </Grid>
                        </Grid>

                        <Button variant="contained" disabled sx={{m:3}}>
                        View Project
                      </Button>
                      </ Container>
                  </ Grid>
                  <Grid item xs={12} sm={12} md={5} >
                    <Paper sx={{boxShadow:'0 8px 30px rgba(0,0,0,0.32)', borderRadius:'5px', p:3, lineHeight:0}} >
                      <ReactPlayer width='100%' height='100%' playing loop muted={true} controls={false} playsinline={true} url={'https://ss.simplestuff.co.nz/wp-content/uploads/2021/09/test-coin.mp4'} type="video/mp4" />
                    </Paper>
                  </ Grid>
                </ Grid>
            </ Container>

</ Container>

          </Box>

        </main>
          {/* Footer */}
          <Box sx={{ bgcolor: '#FAFAFA', p: 6 }} component="footer">
           <Typography variant="h6" align="center" gutterBottom>
             GodRoll
           </Typography>
           <Typography
             variant="subtitle1"
             align="center"
             color="text.secondary"
             component="p"
           >
             Built for the Solana community. (v0.1.6)
           </Typography>
          </Box>
         {/* End footer */}
      </div>
    </ThemeProvider>
  );
}

export default App;
